<template>
  <div class="sokoban">
    <div class="game">
      <h2>{{step?`第${currentGame}关-已走${step}步`:`推箱子-第${currentGame}关`}}</h2>
      <p>小键盘 上下左右 操作</p>
      <div class="box" ref="boxDom">
        <ul v-for="(item,itemIndex) in map" :key="itemIndex">
          <li
            ref="gameDom"
            v-for="(child,childIndex) in item"
            :key="childIndex"
            :class="classSelect(child)"
          >
            <i v-if="child===4||child===6" :class="{ac:child===6}"></i>
            <em v-if="child===3||(child===5&&(nextWay===3||nextWay===6))"></em>
            <b v-if="child===5"></b>
          </li>
        </ul>
      </div>
      <div class="btn">
        <fx-button type="default" @clickEvt="reset">重置</fx-button>
        <div class="op" v-if="!Utils.isPC()">
          <em @click.stop="operating(37)">左</em>
          <span>
            <em @click.stop="operating(38)">上</em>
            <em @click.stop="operating(40)">下</em>
          </span>
          <em @click.stop="operating(39)">右</em>
        </div>
        <!-- <fx-button type="default" @clickEvt="goBack">回退</fx-button> -->
      </div>
    </div>
    <fx-modal title="恭喜过关" v-if="modal" @cancel="modal=false" @define="nextGame(currentGame+1)">
      <p>恭喜过关,点击下一关</p>
    </fx-modal>
  </div>
</template>

<script>
// 0墙 1路 2虚无 3定点 4箱子 5人 6箱子加定点
import gameMap from 'assets/sokobanData.js'
export default {
  name: 'sokoban',
  data() {
    return {
      // 记录行动
      historyList: [],
      // 当前关卡
      currentGame: 1,
      // 所有关卡数据
      gameList: gameMap,
      // 获胜箱子数
      winGame: 0,
      // 当前游戏地图
      map: [],
      // 下一步方向
      nextWay: 1,
      // 当前自己
      theWayMe: 1,
      // 显示下一关
      modal: false,
      // 已走步数
      step: 0
    }
  },
  methods: {
    // 是否成功
    isSuccess() {
      let num = 0
      this.map.forEach(item => {
        item.forEach(child => {
          if (child === 6) num++
        })
      })
      return num === this.winGame
    },
    // className
    classSelect(num) {
      switch (Number(num)) {
        case 0:
          return 'ac'
        case 2:
          return 'no'
        default:
          return ''
      }
    },
    // 移动操作
    operating(code) {
      let way = this.findWay(this.findMe())
      let nextWay = this.findWay(this.findSide(code))
      switch (code) {
        case 37:
          if (way.left === 1 || way.left === 3) this.go(code)
          if ((way.left === 6 || way.left === 4) && (nextWay.left === 1 || nextWay.left === 3))
            this.goBox(code, way.left)
          break
        case 38:
          if (way.top === 1 || way.top === 3) this.go(code)
          if ((way.top === 6 || way.top === 4) && (nextWay.top === 1 || nextWay.top === 3))
            this.goBox(code, way.top)
          break
        case 39:
          if (way.right === 1 || way.right === 3) this.go(code)
          if ((way.right === 6 || way.right === 4) && (nextWay.right === 1 || nextWay.right === 3))
            this.goBox(code, way.right)
          break
        case 40:
          if (way.bottom === 1 || way.bottom === 3) this.go(code)
          if (
            (way.bottom === 6 || way.bottom === 4) &&
            (nextWay.bottom === 1 || nextWay.bottom === 3)
          )
            this.goBox(code, way.bottom)
          break
        case 32: // space
          this.reset()
          break
        case 90:
          this.goBack()
          break
        default:
          break
      }
      if (this.isSuccess()) this.modal = true
    },
    // keyCode
    keyDown(ev) {
      this.operating(Number(ev.keyCode))
    },
    findWay(me) {
      if (!me || !me.length) return
      let left = [me[0], me[1] - 1 < 0 ? 0 : me[1] - 1]
      let right = [me[0], me[1] + 1 > this.map[me[0]].length ? this.map[me[0]].length : me[1] + 1]
      let top = [me[0] - 1 < 0 ? 0 : me[0] - 1, me[1]]
      let bottom = [me[0] + 1 > this.map.length ? this.map.length : me[0] + 1, me[1]]
      let obj = {}
      this.map.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          if (left[0] === itemIndex && left[1] === childIndex) obj.left = child
          if (right[0] === itemIndex && right[1] === childIndex) obj.right = child
          if (top[0] === itemIndex && top[1] === childIndex) obj.top = child
          if (bottom[0] === itemIndex && bottom[1] === childIndex) obj.bottom = child
        })
      })
      return obj
    },
    findMe() {
      let arr = []
      this.map.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          if (child === 5) arr = [itemIndex, childIndex]
        })
      })
      return arr
    },
    // 运动方向
    findSide(type) {
      const typeArr = [37, 38, 39, 40]
      if (!typeArr.includes(type)) return
      let findArr = this.findMe()
      switch (type) {
        case 37:
          return [findArr[0], findArr[1] - 1]
        case 38:
          return [findArr[0] - 1, findArr[1]]
        case 39:
          return [findArr[0], findArr[1] + 1]
        case 40:
          return [findArr[0] + 1, findArr[1]]
        default:
          break
      }
    },
    // 人位置
    go(type) {
      let index = {}
      this.map.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          if (child === 5 && type === 37) index.next = [itemIndex, childIndex - 1]
          if (child === 5 && type === 38) index.next = [itemIndex - 1, childIndex]
          if (child === 5 && type === 39) index.next = [itemIndex, childIndex + 1]
          if (child === 5 && type === 40) index.next = [itemIndex + 1, childIndex]
          if (child === 5) index.me = [itemIndex, childIndex]
        })
      })
      let way = JSON.parse(JSON.stringify(this.map[index.next[0]][index.next[1]]))
      this.nextWay = this.map[index.next[0]][index.next[1]]
      this.map[index.next[0]][index.next[1]] = 5
      this.map[index.me[0]][index.me[1]] = this.theWayMe
      this.theWayMe = way === 6 || way === 3 ? 3 : 1
      this.map = JSON.parse(JSON.stringify(this.map))
      this.step++
      this.historyList.push(this.map)
    },
    // 箱子位置
    goBox(code, fromWay = 1) {
      let arr = this.findSide(code)
      let next = this.findWay(this.findSide(code))
      let boxType = 4
      let index = {}
      this.map.forEach((item, itemIndex) => {
        item.forEach((child, childIndex) => {
          if (arr[0] === itemIndex && arr[1] === childIndex && code === 37)
            index.next = [itemIndex, childIndex - 1]
          if (arr[0] === itemIndex && arr[1] === childIndex && code === 38)
            index.next = [itemIndex - 1, childIndex]
          if (arr[0] === itemIndex && arr[1] === childIndex && code === 39)
            index.next = [itemIndex, childIndex + 1]
          if (arr[0] === itemIndex && arr[1] === childIndex && code === 40)
            index.next = [itemIndex + 1, childIndex]
          if (arr[0] === itemIndex && arr[1] === childIndex) index.me = [itemIndex, childIndex]
        })
      })
      if (code === 37 && next.left === 3) boxType = 6
      if (code === 38 && next.top === 3) boxType = 6
      if (code === 39 && next.right === 3) boxType = 6
      if (code === 40 && next.bottom === 3) boxType = 6
      this.map[index.next[0]][index.next[1]] = boxType
      this.map[index.me[0]][index.me[1]] = fromWay
      this.go(code)
    },
    // 计算格子大小
    initMap() {
      const row = this.map[0].length
      const col = this.map.length
      let width = this.$refs.boxDom.offsetWidth / row
      let height = this.$refs.boxDom.offsetHeight / col
      if (!this.Utils.isPC()) {
        let clientW = document.documentElement.clientWidth;
        let clientH = document.documentElement.clientHeight;
        let clientDis = clientW >= clientH ? clientH : clientW;
        this.$refs.boxDom.style.width = `${clientDis}px`;
        this.$refs.boxDom.style.height = `${clientDis}px`;
        width = clientDis / row
        height = clientDis / col
      }
      const dis = width >= height ? height : width
      this.$refs.gameDom.forEach(item => {
        item.style.width = `${dis}px`
        item.style.height = `${dis}px`
      })
    },
    // 下一关
    nextGame(index = this.currentGame) {
      this.historyList = []
      if (index - 1 >= this.gameList.length) {
        this.$success('恭喜通过全部关卡!')
        this.modal = false
        return
      }
      const map = JSON.parse(JSON.stringify(this.gameList[index - 1].game))
      const winGame = JSON.parse(JSON.stringify(this.gameList[index - 1].win))
      this.map = map
      this.winGame = winGame
      this.step = 0
      setTimeout(() => {
        this.initMap()
        this.modal = false
        this.currentGame = index
      }, 30)
    },
    // 重置
    reset() {
      this.nextGame()
    },
    // 回退
    goBack() {
      if (!this.historyList.length) {
        this.$warning('已经无法再退了!')
        this.reset()
        return
      }
      this.isCurrent(this.historyList[this.historyList.length - 1])
      this.map = JSON.parse(JSON.stringify(this.historyList.pop()))
    },
    // 是否是当前位置
    isCurrent(arr) {
      const off = arr.every((item, itemIndex) => {
        return item.every((child, childIndex) => {
          return this.map[itemIndex][childIndex] === child
        })
      })
      if (off) this.historyList.pop()
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.nextGame()
      window.addEventListener('keydown', this.keyDown, true)
    })
  },
  destroyed() {
    window.removeEventListener('keydown', this.keyDown, true)
  }
}
</script>

<style lang="less" scoped>
.sokoban {
  > .game {
    width: fit-content;
    height: fit-content;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    text-align: center;
    > .box {
      width: 600px;
      height: 600px;
      > ul {
        width: 100%;
        > li {
          box-sizing: border-box;
          float: left;
          width: 60px;
          height: 60px;
          border: 1px solid #ccc;
          box-sizing: border-box;
          text-align: center;
          position: relative;
          &.ac {
            background-color: rgba(51, 51, 51, 0.7);
          }
          &.no {
            background-color: rgba(153, 153, 153, 0.7);
          }
          > em {
            display: inline-block;
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            &::before,
            &::after {
              content: "";
              width: 40px;
              height: 1px;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              margin: auto;
              background-color: #0f0;
            }
            &::before {
              transform: rotate(-45deg);
            }
            &::after {
              transform: rotate(45deg);
            }
          }
          > i {
            display: inline-block;
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            border: 6px solid #8e6521;
            background-color: #c49636;
            position: relative;
            &::before,
            &::after {
              content: "";
              width: 100%;
              height: 4px;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              margin: auto;
              background-color: #d1a64b;
              transform-origin: center center;
              box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
            }
            &::before {
              transform: rotate(-45deg);
            }
            &::after {
              transform: rotate(45deg);
            }
            &.ac {
              border: 6px solid #5cc725;
              background-color: #7bd24f;
              &::before,
              &::after {
                background-color: #5cc725;
              }
            }
          }
          > b {
            display: inline-block;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            background-color: #f00;
            box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
            position: absolute;
            left: 0;
            bottom: 0;
            top: 0;
            right: 0;
            margin: auto;
            z-index: 10;
          }
        }
      }
    }
    > .btn {
      width: fit-content;
      margin: 0 auto;
      text-align: center;
      padding-top: 20px;
      display: flex;
      > .op {
        padding-left: 20px;
        display: flex;
        text-align: center;
        em {
          display: block;
          width: 50px;
          height: 100px;
          line-height: 100px;
          background-color: #fff;
          border: 1px solid #ccc;
          box-sizing: border-box;
        }
        > span {
          display: block;
          width: 50px;
          height: 100px;
          display: flex;
          flex-direction: column;
          > em {
            width: 50px;
            height: 50px;
            line-height: 50px;
          }
        }
      }
    }
  }
}
</style>